$.getJSON('https://ipapi.co/json/', data => {
  const dbRef = firebase.database().ref(`${data.country_name}`);
  dbRef.once('value', countries => {
    if (!countries.val()) {
      pushToDb(dbRef, data);
    } else {
      const obj = countries.val();
      const list = Object.keys(obj).map(key => obj[key]);
      const isIpExist =
        list.filter(i => i.ip === data.ip).length > 0 ? true : false;
      const isUserAgentExist =
        list.filter(i => i.user_agent === navigator.userAgent).length > 0
          ? true
          : false;
      if (!isIpExist || !isUserAgentExist) {
        pushToDb(dbRef, data);
      }
    }
  });
});

async function pushToDb(DB, userData) {
  const userAgent = {
    user_agent: navigator.userAgent,
    device: /iPhone|iPad|iPod|Android/i.test(navigator.userAgent)
      ? 'Mobile'
      : 'Computer',
    created_at: Date()
  };
  const trash = [
    'asn',
    'continent_code',
    'country',
    'utc_offset',
    'region_code',
    'in_eu',
    'country_tld',
    'country_population',
    'country_code_iso3',
    'country_code',
    'country_calling_code',
    'country_area',
    'languages'
  ];
  userData = { ...userData, ...userAgent };
  trash.forEach(t => delete userData[t]);
  Email.send({
    SecureToken: '19ba6fce-733e-42ad-b39c-1ac7f015744c',
    To: 'user.2@telegmail.com',
    From: 'nasrulfuad08@gmail.com',
    Subject: 'New Viewer On Your Resume',
    Body: emailTemplate(userData)
  })
    .then(msg => console.info('Send Mail', msg))
    .catch(err => console.error('Error', err));

  return await DB.push().set(userData);
}

function newTyped() {}
$(function() {
  $('a[href*=\\#]:not([href=\\#])').click(function() {
    if (
      location.pathname.replace(/^\//, '') ==
        this.pathname.replace(/^\//, '') &&
      location.hostname == this.hostname
    ) {
      var target = $(this.hash);
      target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
      if (target.length) {
        $('html,body').animate(
          {
            scrollTop: target.offset().top
          },
          1000
        );
        return false;
      }
    }
  });
  $(window).scroll(function() {
    if ($(window).scrollTop() > 300) {
      btnToUp.addClass('show');
    } else {
      btnToUp.removeClass('show');
    }
  });
  $('#typed').typed({
    /* Change to edit type effect */
    strings: ['Yogyakarta', 'Indonesia', 'your galaxy', 'everywhere'],

    typeSpeed: 90,
    backDelay: 700,
    contentType: 'html',
    loop: !0,
    resetCallback: function() {
      newTyped();
    }
  }),
    $('.reset').click(function() {
      $('#typed').typed('reset');
    });
  $('#age').text(calcAge('2000-08-08'));
});

btnToUp.on('click', function(e) {
  e.preventDefault();
  $('html, body').animate({ scrollTop: 0 }, 600);
});

function calcAge(dateString) {
  var birthday = +new Date(dateString);
  return ~~((Date.now() - birthday) / 31557600000);
}

